html {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  position: relative;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-list-link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  gap: 20px;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

:root {
  --repeated-color: #00acdc;
}

.nav_link:hover, .nav_link.is-active, .nav-list-link:hover {
  color: var(--repeated-color);
}

.nav-item {
  margin-right: 20px;
  display: inline-block;
}

.nav-item:last-child {
  margin: 0;
}

.logo-img {
  width: 40px;
  height: 40px;
  display: block;
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--repeated-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav-item:first-child .nav-list-link {
  color: var(--repeated-color);
}
